import * as opuService from "@/service/opu";

const formatToLineObject = (lineStr) => {
    return lineStr
        .split("|")
        .map((item) => item.split("~"))
        .map((item, index) => {
            let obj = {};
            obj[`dose${index + 1}`] = item[0];
            obj[`time${index + 1}`] = item[1];
            obj[`initial${index + 1}`] = item[0];
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

const timeLineFormatter = (lineStr) => {
    return lineStr
        .split("|")
        .map((item) => item.split("~"))
        .map((item, index) => {
            let obj = {};
            obj[`timeInit${index + 1}`] = {
                part1: item[0],
                part2: item[1]
            };
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

const bpLineFormatter = (lineStr) => {
    return lineStr
        .split("|")
        .map((item, index) => {
            let obj = {};
            obj[`timeInit${index + 1}`] = item;
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

const o2LineFormatter = (lineStr) => {
    return lineStr
        .split("|")
        .map((item, index) => {
            let obj = {};
            obj[`timeInit${index + 1}`] = item;
            return obj;
        })
        .reduce((acc, item) => {
            return Object.assign(acc, item);
        }, {});
};

